@charset "UTF-8";
@use "sass:math";
@use "common/00_base/_00_index" as *;

//
// footer
//

.footer {
  background: #3C3C3C;
  color: #FFFFFF;
  text-align: center;
  padding: 86px 10px 140px;

  @include mq('small') {
    text-align: left;
    padding: vw-s(59) vw-s(63) vw-s(110) vw-s(50);
  }
}

.footer_nav {
  display: flex;
  justify-content: center;
  gap: 40px;

  @include mq('small') {
    gap: vw-s(33);
    flex-direction: column;
    line-height: 1.5;
  }
}

.footer_nav_a {
  .e-ico {
    display: inline-block;
    width: 21px;
    height: 15px;
    background-image: url("data:image/svg+xml,%3Csvg width='21' height='15' viewBox='0 0 21 15' fill='none' xmlns='http://www.w3.org/2000/svg'%3E%3Crect x='3.8143' y='1' width='15.9714' height='9.8' stroke='white'/%3E%3Cpath d='M1 4.74268V13.9998H17.2' stroke='white'/%3E%3C/svg%3E%0A");
    background-position: center;
    background-repeat: no-repeat;
    background-size: cover;
    margin-left: 10px;
    position: relative;
    top: 5px;

    @include mq('small') {
      top: vw-s(1);
    }
  }

  font-size: 16px;
  line-height: 1.1;
  text-decoration: underline;

  &:hover {
    text-decoration: none;
  }

  @include mq('small') {
    @include fz-s(16);
    line-height: calc(26/16);

    &:last-of-type {
      .e-ico {
        top: vw-s(2);
        left: vw-s(8);
      }
    }
  }
}

.footer_coryright {
  font-size: 14px;
  line-height: 1.1;
  margin: 60px 0 0;

  @include mq('small') {
    @include fz-s(14);
    margin-top: vw-s(53);
    margin-left: vw-s(12);
    white-space: nowrap;
  }
}