@charset "UTF-8";
@use "sass:math";
@use "common/00_base/_00_index" as *;

//
// common
//

body {
  font-family: "Noto Sans JP", sans-serif;
  font-size: 16px;
  font-weight: 400;
  line-height: 1.4;
  font-optical-sizing: auto;
  font-style: normal;
  -webkit-text-size-adjust: 100%;
  color: #333333;
  overflow-x: hidden;

  @include mq('small') {
    @include fz-s(12);
  }
}

a {
  text-decoration: none;
  &:hover {
    text-decoration: none;
  }
}

img, svg {
  max-width: 100%;
  height: auto;
}

b, strong {
  font-weight: bold;
}

sup {
  font-size: 75%;
  vertical-align: top;
}

sub {
  font-size: 75%;
  vertical-align: bottom;
}

.c_img_cover {
  width: 100%;
  height: 100%;
  object-fit: cover;
}

.c_w_100 {
  width: 100%;
}

.l_container {
  max-width: 1440px;
  width: 100%;
  margin: 0 auto;
}

.c_inner {
  max-width: 1240px;
  width: 100%;
  margin: 0 auto;

  @include mq('small') {
    padding: 0  vw-s(20);
  }
}

/* Scale layout */
@include mq-gt('small') {
  html, body {
    min-width: #{ $break-large }px;
  }
}

/* js animation up */
.js-anima-up {
  transform: translateY(20px);
  opacity: 0;
  transition: transform 1s ease-out, opacity 1s ease-out;
  will-change: transform, opacity;

  &-on {
    transform: translateY(0);
    opacity: 1;
  }
}
